import './home.css';
import './home.m.css';

import Profile from '../../assets/profile.jpg';
import Github from '../../assets/github.png';
import Facebook from '../../assets/facebook.png';
import Linkedin from '../../assets/linkedin.png';
import Instagram from '../../assets/instagram.png';

import React, { useEffect, useState } from 'react';

const works = [
    'Front-end engineer',
    // 'Back-end developr'
];
export const Home = () => {
    let [ work, setWork ] = useState('');
    const typeWriter = () => {
        for(let w of works) {
            const letras = w.split('');
            letras.forEach((letra, i) => {
                setTimeout(() => {
                    work += letra
                    setWork(work);
                }, 80 * i);
            });
        }
    };
    useEffect(() => typeWriter(), []);
    return(
        <section id="home">
            <div className="home__profile">
                <img className="home__image" src={Profile} alt="Profile" />
            </div>
            <hgroup className="home__title">
                <h2 className="home__name">I'm Giovane Santos</h2>
                <h3 className="home__work">
                    {work} <div className="home__cursor"/>
                </h3>
            </hgroup>
            <div className="home__links">
                <a target="blank" href="https://github.com/GIovaneSantosSilva" title="GitHub">
                    <img className="image" src={Github} alt="Github"/>
                </a>
                <a target="blank" href="https://www.facebook.com/giovane.santossilva.7/" title="Facebook">
                    <img className="image" src={Facebook} alt="Facebook"/>
                </a>
                <a target="blank" href="https://www.instagram.com/giovanesantossilva/" title="Instagram">
                    <img className="image" src={Instagram} alt="Instagram"/>
                </a>
                <a target="blank" href="https://www.linkedin.com/in/giovane-santos-silva-a63790156/" title="Linkedin"> 
                    <img className="image" src={Linkedin} alt="Linkedin"/>
                </a>
            </div>
        </section>
    );
};