import './index.css';

import React from 'react';

import { Header } from '../helpers/header';
import { Home } from './home';
import { About } from './about';
import { Services } from './services';

class App extends React.Component {
    
    constructor(props) {
        super(props);
        this.state = {
            scrollTop: true
        };
        this._target = [];
    }

    _debounce(fn, wait, immediate) {
        let timeout;
        return function(...args) {
            const self = this;
            const later = function () {
                timeout = null;
                if(!immediate) fn.apply(self, args);
            };
            const callNow = immediate && !timeout;
            clearTimeout(timeout);
            timeout = setTimeout(later, wait);
            if(callNow) fn.apply(self, args);
        }
    }

    _animateScroll() {
        const diff = (window.innerHeight * 3)/4;
        const windowTop = window.pageYOffset + diff;
        console.log(this._target);
        this._target.forEach((el) => {
            if(windowTop > el.offsetTop) {
                el.classList.add('animate');
            } else {
                el.classList.remove('animate');
            }
        });
;    }

    _onScroll() {
        window.onscroll = this._debounce(() => {
            if(this._target.length) {
                this._animateScroll();
            }
            this.setState({ scrollTop: window.scrollY === 0 });
        }, 100);
    }

    componentDidMount() {
        this._onScroll();
        this._target = document.querySelectorAll('[data-anime]');
    }

    render() {
        const { scrollTop } = this.state;
        return (
            <>
                <Header scrollTop={scrollTop} />
                <main>
                    <Home />
                    <About />
                    <Services />
                </main>
                <footer>
                    
                </footer>
            </>
        );
    }

}

export default App;
